import { useEffect, useState } from 'react';
import { CaptchaApi, LoginApi } from 'utils/Api';
import { useNavigate } from 'react-router-dom';
import { Card, Form, Input, Button } from 'antd';

const Login = () => {
  const [captchaCode, setCaptchaCode] = useState(null);
  const navigate = useNavigate();

  useEffect(() => {
    getCaptcha();
  }, []);

  const getCaptcha = () => {
    CaptchaApi().then((res) => {
      setCaptchaCode(res.data.svg);
      localStorage.setItem('__admin_token', res.data.token);
    });
  };

  const onFinish = (value) => {
		
      LoginApi(value)
        .then((res) => {
          localStorage.setItem('__admin_token', res.data.token);
          navigate('/admin');
        })
        .catch(() => {
          getCaptcha();
        });
  };


  return (
    <Card title="后台管理系统" bordered={false} style={{ width: 600, margin: '8rem auto', textAlign: 'center' }}>
      <Form
        name="basic"
				labelCol={{span: 4}}
        initialValues={{ remember: true }}
        onFinish={onFinish}
        autoComplete="off"
      >
        <Form.Item
          label="用户名"
          name="username"
          rules={[{ required: true, message: '请输入用户名!' }]}
        >
          <Input />
        </Form.Item>

        <Form.Item
          label="密码"
          name="passwd"
          rules={[{ required: true, message: '请输入密码' }]}
        >
          <Input.Password />
        </Form.Item>
        <Form.Item label="验证码" style={{ marginBottom: 0 }}>
          <Form.Item
            name="captchaCode"
            rules={[{ required: true }]}
            style={{ display: 'inline-block', width: 'calc(50% - 8px)' }}
          >
            <Input placeholder="请输入验证码" />
          </Form.Item>
          <Form.Item
            style={{
              display: 'inline-block',
              width: 'calc(50% - 8px)',
              margin: '0 8px',
            }}
          >
            {captchaCode && (
              <div
                dangerouslySetInnerHTML={{ __html: captchaCode }}
                onClick={() => getCaptcha()}
              ></div>
            )}
          </Form.Item>
        </Form.Item>
        <Form.Item wrapperCol={{  span: 16 }}>
          <Button type="primary" htmlType="submit">
            登陆
          </Button>
        </Form.Item>
      </Form>
    </Card>
  );
};

export default Login;
